import Header from './Header';
import React from 'react';
import { Route, Switch } from 'react-router-dom';
import DeviceList from './DeviceList';
import Device from './Device';
import ModuleList from './ModuleList';
import KernelList from './KernelList';
import Kernel from './Kernel';
import JobInfoList from './JobInfoList';
import JobInfo from './JobInfo';
import SystemInfo from './SystemInfo';

import './App.scss';

class App extends React.Component {
 
	render() {
		return (
			<div>
				<Header/>
				<Switch>
					<Route path='/device_list' component={DeviceList} />
					<Route path='/device' component={Device} />
					<Route path='/module_list' component={ModuleList} />
					<Route path='/kernel_list' component={KernelList} />
					<Route path='/kernel' component={Kernel} />
					<Route path='/job_status_list' component={JobInfoList} />
					<Route path='/job_status' component={JobInfo} />
					<Route path='/system_info' component={SystemInfo} />
					<Route path='/' component={SystemInfo} />
				</Switch>
			</div>
		);
	}
}

export default App;
