import React from 'react';
import MaterialTable from 'material-table';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import agent from '../agent';


class DeviceList extends React.Component {
	
	constructor() {
		super();
		this.state = {
	    	device_columns: [
				{ title: 'Property', field: 'key' },
	      	  	{ title: 'Value', field: 'value' }
	    	],
			
			device_property_list: [],
			
			error: ''
		}
		
		this.setDevice = this.setDevice.bind(this);
		this.setError = this.setError.bind(this);
	}
	
	setDevice(res) {
		if(res.body['status'] === 0)
		{
			let device_property_list = res.body['devicePropertyList'];
			this.setState(prevState => ({...prevState, error:'', device_property_list:device_property_list}));
		}
		else
		{
			this.setState(prevState => ({...prevState, error:res.body["error"]}));
		}
	}

	setError(error) {
		this.setState(prevState => ({...prevState, error:error.message}));
	}
	
	componentDidMount () {
		agent.csynRequests.device(this.props.location.state.ipAddress, this.props.location.state.port, this.setDevice, this.setError);
	}
		
	render() {
  	  	return (
			<div>
				<Grid container spacing={3} direction="column" alignContent="center">
					<Grid item>
    					<MaterialTable title="Device Property List" columns={this.state.device_columns} data={this.state.device_property_list} options={{search: false}}/>
					</Grid>
					<Grid item>
						<Typography variant="body2">{this.state.error}</Typography>
					</Grid>
				</Grid>
			</div>
		);
	}
}

export default DeviceList;
