import React from 'react';
import MaterialTable from 'material-table';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import agent from '../agent';


class Kernel extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
	    	kernel_list_columns: [
				{ title: 'Kernel Name', field: 'kernelName' },
				{ title: 'Module Name', field: 'moduleName' },
	      	  	{ title: 'Module Version', field: 'moduleVersion' },
				{ title: 'Module Type', field: 'moduleType', type: 'numeric'},
				{ title: 'Kernel Description', field: 'kernelDescription' }
	    	],
			
			kernel_list: [],
			
	    	parameter_list_columns: [
				{ title: 'Parameter Name', field: 'parameterName' },
				{ title: 'Parameter Description', field: 'parameterDescription' },
				{ title: 'Order', field: 'order' , type: 'numeric'},
	      	  	{ title: 'Data Type', field: 'dataType' },
				{ title: 'Parameter Type', field: 'parameterType'}
	    	],
			
			parameter_list: [],
			
			error: ''
		}
		
		this.setKernel = this.setKernel.bind(this);
		this.setError = this.setError.bind(this);
	}

	setKernel(res) {
		if(res.body['status'] === 0)
		{
			let kernel_list = res.body['kernelList'];
			let parameter_list = res.body['parameterList'];
			this.setState(prevState => ({...prevState, error:'', kernel_list:kernel_list, parameter_list:parameter_list}));
		}
		else
		{
			this.setState(prevState => ({...prevState, error:res.body["error"]}));
		}
	}

	setError(error) {
		this.setState(prevState => ({...prevState, error:error.message}));
	}
	
	componentDidMount () {
		agent.csynRequests.kernel(this.props.location.state.kernelName, this.props.location.state.moduleName, this.props.location.state.moduleVersion, this.props.location.state.moduleType, this.setKernel, this.setError);
	}
	
	
	render() {

  	  	return (
			<div>
				<Grid container spacing={3} direction="column" alignContent="center">
					<Grid item>
    					<MaterialTable title="Kernel" columns={this.state.kernel_list_columns} data={this.state.kernel_list} options={{search: false, paging: false}}/>
					</Grid>
					<Grid item>
    					<MaterialTable title="Kernel Parameters" columns={this.state.parameter_list_columns} data={this.state.parameter_list} options={{search: false}}/>
					</Grid>
					<Grid item>
						<Typography variant="body2">{this.state.error}</Typography>
					</Grid>
				</Grid>
			</div>
		);
	}
}

export default Kernel;