import React from 'react';
import MaterialTable from 'material-table';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import { Redirect } from 'react-router-dom'
import agent from '../agent';


class DeviceList extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
	    	device_list_columns: [
				{ title: 'Ip Address', field: 'ipAddress' },
	      	  	{ title: 'Port', field: 'port', type: 'numeric'},
	      	  	{ title: 'Device Name', field: 'name'},
		  		{ title: 'Major', field: 'major', type: 'numeric'},
				{ title: 'Minor', field: 'minor', type: 'numeric'}
	    	],
			device_list: [],
			error: '',
			redirect: 0,
			redirect_port: 0,
			redirect_ipAddress: ''
			
		}
		
		this.setDeviceList = this.setDeviceList.bind(this);
		this.setError = this.setError.bind(this);
		this.gotoDevice = this.gotoDevice.bind(this);
	}

	setDeviceList(res) {
		if(res.body['status'] === 0)
		{
			let device_list = res.body['list'];
			this.setState(prevState => ({...prevState, error:'', device_list:device_list}));
		}
		else
		{
			this.setState(prevState => ({...prevState, error:res.body["error"]}));
		}
	}

	setError(error) {
		this.setState(prevState => ({...prevState, error:error.message}));
	}
	
	componentDidMount () {
		agent.csynRequests.deviceList(this.setDeviceList, this.setError);
	}
	
	gotoDevice(event, rowData) {
		this.setState(prevState => ({...prevState, redirect:1, redirect_port:rowData.port, redirect_ipAddress: rowData.ipAddress}));
	}
		
	render() {
		
		if (this.state.redirect === 1) {
			return <Redirect to={{pathname: '/device', state: {ipAddress: this.state.redirect_ipAddress, port:this.state.redirect_port }}}/>
		}
		
  	  	return (
			<div>
				<Grid container spacing={3} direction="column" alignContent="center">
					<Grid item>
    					<MaterialTable title="Device List" columns={this.state.device_list_columns} data={this.state.device_list} options={{search: false}}
								actions={[{ icon: 'info', tooltip: 'Device property list', onClick: this.gotoDevice}]}/>
					</Grid>
					<Grid item>
						<Typography variant="body2">{this.state.error}</Typography>
					</Grid>
				</Grid>
			</div>
		);
	}
}

export default DeviceList;
