import React from 'react';
import Button from '@material-ui/core/Button';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import { Link } from 'react-router-dom';

class DownloadMenu extends React.Component {
   
  constructor(props) {
		super(props);
		this.state = {
			anchorEl:null
		}
		
		this.handleClick = this.handleClick.bind(this);
		this.handleClose = this.handleClose.bind(this);
	}

	handleClick(event) {
		this.setState({anchorEl:event.currentTarget})
	}
	
	handleClose() {
		this.setState({anchorEl:null})
	}
	
	render() {
		return (
			<div>
				<Button aria-controls="simple-menu" aria-haspopup="true" onClick={this.handleClick} color="inherit">Download</Button>
				<Menu id="simple-menu" anchorEl={this.state.anchorEl} keepMounted open={Boolean(this.state.anchorEl)} onClose={this.handleClose} >
					<MenuItem component={Link} to="/sdk/csyn_cpp_win_client.zip" target="_blank" onClick={this.handleClose} color="inherit">C++ SDK for Windows</MenuItem>
					<MenuItem component={Link} to="/sdk/csyn_cpp_linux_ubuntu_client.tar.gz" target="_blank" onClick={this.handleClose} color="inherit">C++ SDK for Ubuntu Linux</MenuItem>
					<MenuItem component={Link} to="/sdk/csyn_csharp_client.zip" target="_blank" onClick={this.handleClose} color="inherit">C# SDK</MenuItem>
					<MenuItem component={Link} to="/sdk/csyn_python_client.zip" target="_blank" onClick={this.handleClose} color="inherit">Python SDK</MenuItem>
				</Menu>
			</div>
		);
	}
}

export default DownloadMenu;