import React from 'react';
import MaterialTable from 'material-table';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import agent from '../agent';

class SystemInfo extends React.Component {
	
	constructor() {
		super();
		this.state = {
	    	system_status_columns: [
				{ title: 'Version', field: 'systemVersion' },
	      	  	{ title: 'Status', field: 'systemStatus' }
	    	],
			
			system_status_list: [],
			
	    	engine_status_columns: [
				{ title: 'IP Address', field: 'ipAddress' },
	      	  	{ title: 'Port', field: 'port', type: 'numeric' },
				{ title: 'Device Id', field: 'deviceId', type: 'numeric' },
				{ title: 'Device Name', field: 'deviceName' },
	      	  	{ title: 'Engine Status', field: 'engineStatus' },
				{ title: 'Engine Message', field: 'engineMessage' }
	    	],
			engine_status_list: [],
			
			error: ''
		}
		
		this.setSystemInfo = this.setSystemInfo.bind(this);
		this.setError = this.setError.bind(this);
	}
		
	setSystemInfo(res) {
		if(res.body['status'] === 0)
		{
			let system_status_list = res.body['masterSystemInfoList'];
			let engine_status_list = res.body['masterEngineInfoList'];
			this.setState(prevState => ({...prevState, error:'', system_status_list:system_status_list, engine_status_list:engine_status_list}));
		}
		else
		{
			this.setState(prevState => ({...prevState, error:res.body["error"]}));
		}
	}

	setError(error) {
		this.setState(prevState => ({...prevState, error:error.message}));
	}
	
	componentDidMount () {
		agent.csynRequests.systemInfo(this.setSystemInfo, this.setError);
	}
		
	render() {
  	  	return (
			<div>
				<Grid container spacing={3} direction="column" alignContent="center">
					<Grid item>
    					<MaterialTable title="System Status" columns={this.state.system_status_columns} data={this.state.system_status_list} options={{search: false, paging: false}}/>
					</Grid>
					<Grid item>
		    			<MaterialTable title="Engines Status" columns={this.state.engine_status_columns} data={this.state.engine_status_list} options={{search: false}}/>
					</Grid>
					<Grid item>
						<Typography variant="body2">{this.state.error}</Typography>
					</Grid>
				</Grid>
			</div>
		);
	}
}

export default SystemInfo;