import React from 'react';
import MaterialTable from 'material-table';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import agent from '../agent';


class JobInfo extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
	    	job_info_list_columns: [
				{ title: 'Job Name', field: 'jobName' },
	      	  	{ title: 'UUID', field: 'uuid' },
				{ title: 'Start Time', field: 'startTimestamp'},
				{ title: 'End Time', field: 'endTimestamp' },
				{ title: 'Execution Time', field: 'execution' },
				{ title: 'Status', field: 'executionStatus' },
				{ title: 'Execution Error', field: 'executionError' }
	    	],
			
			job_info_list: [],
			
	    	task_info_list_columns: [
				{ title: 'Task Name', field: 'taskName' },
				{ title: 'UUID', field: 'uuid' },
				{ title: 'Start Time', field: 'startTimestamp'},
				{ title: 'Execution Time', field: 'execution' },
				{ title: 'Ip Address', field: 'ipAddress' },
				{ title: 'Port', field: 'port' },
				{ title: 'Status', field: 'executionStatus' },
				{ title: 'Execution Error', field: 'executionError' }
	    	],
			
			task_info_list: [],
			
			error: ''
		}
		
		this.setJobInfo = this.setJobInfo.bind(this);
		this.setError = this.setError.bind(this);
	}

	setJobInfo(res) {
		if(res.body['status'] === 0)
		{
			let job_info_list = res.body['jobStatusList'];
			let task_info_list = res.body['taskStatusList'];
			this.setState(prevState => ({...prevState, error:'', job_info_list:job_info_list, task_info_list:task_info_list}));
		}
		else
		{
			this.setState(prevState => ({...prevState, error:res.body["error"]}));
		}
	}

	setError(error) {
		this.setState(prevState => ({...prevState, error:error.message}));
	}
	
	componentDidMount () {
		agent.csynRequests.jobInfo(this.props.location.state.uuid, this.setJobInfo, this.setError);
	}
	
	
	render() {

  	  	return (
			<div>
				<Grid container spacing={3} direction="column" alignContent="center">
					<Grid item>
    					<MaterialTable title="Job Status" columns={this.state.job_info_list_columns} data={this.state.job_info_list} options={{search: false, paging: false}}/>
					</Grid>
					<Grid item>
    					<MaterialTable title="Task Status List" columns={this.state.task_info_list_columns} data={this.state.task_info_list} options={{search: false}}/>
					</Grid>
					<Grid item>
						<Typography variant="body2">{this.state.error}</Typography>
					</Grid>
				</Grid>
			</div>
		);
	}
}

export default JobInfo;