import React from 'react';
import MaterialTable from 'material-table';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import { Redirect } from 'react-router-dom'
import agent from '../agent';


class ModuleList extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
	    	module_list_columns: [
				{ title: 'Module Name', field: 'moduleName' },
	      	  	{ title: 'Module Version', field: 'moduleVersion' },
				{ title: 'Module Type', field: 'moduleType', type: 'numeric'},
				{ title: 'Module Description', field: 'moduleDescription' }
	    	],
			
			module_list: [],
			error: '',
			redirect: 0,
			redirect_moduleName: '',
			redirect_moduleVersion: '',
			redirect_moduleType: 0
		}
		
		this.setModuleList = this.setModuleList.bind(this);
		this.setError = this.setError.bind(this);
		this.gotoKernelList = this.gotoKernelList.bind(this);
	}

	setModuleList(res) {
		if(res.body['status'] === 0)
		{
			let module_list = res.body['list'];
			this.setState(prevState => ({...prevState, error:'', module_list:module_list}));
		}
		else
		{
			this.setState(prevState => ({...prevState, error:res.body["error"]}));
		}
	}

	setError(error) {
		this.setState(prevState => ({...prevState, error:error.message}));
	}
	
	componentDidMount () {
		agent.csynRequests.moduleList(this.setModuleList, this.setError);
	}
	
	gotoKernelList(event, rowData) {
		this.setState(prevState => ({...prevState, redirect:1, redirect_moduleName:rowData.moduleName, redirect_moduleVersion: rowData.moduleVersion, redirect_moduleType: rowData.moduleType}));
	}
	
	render() {
		if (this.state.redirect === 1) {
			return <Redirect to={{pathname: '/kernel_list', state: {moduleName: this.state.redirect_moduleName, moduleVersion:this.state.redirect_moduleVersion, moduleType:this.state.redirect_moduleType }}}/>
		}
		
  	  	return (
			<div>
				<Grid container spacing={3} direction="column" alignContent="center">
					<Grid item>
    					<MaterialTable title="Module List" columns={this.state.module_list_columns} data={this.state.module_list} options={{search: false}}
								actions={[{ icon: 'info', tooltip: 'Kernel list', onClick: this.gotoKernelList}]}/>
					</Grid>
					<Grid item>
						<Typography variant="body2">{this.state.error}</Typography>
					</Grid>
				</Grid>
			</div>
		);
	}
}

export default ModuleList;