import React from 'react';
import MaterialTable from 'material-table';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import { Redirect } from 'react-router-dom'
import agent from '../agent';


class KernelList extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
	    	kernel_list_columns: [
				{ title: 'Kernel Name', field: 'kernelName' },
				{ title: 'Module Name', field: 'moduleName' },
	      	  	{ title: 'Module Version', field: 'moduleVersion' },
				{ title: 'Module Type', field: 'moduleType', type: 'numeric'},
				{ title: 'Kernel Description', field: 'kernelDescription' }
	    	],
			
			kernel_list: [],
			error: '',
			redirect: 0,
			redirect_kernelName: '',
			redirect_moduleName: '',
			redirect_moduleVersion: '',
			redirect_moduleType: 0
		}
		
		this.setKernelList = this.setKernelList.bind(this);
		this.setError = this.setError.bind(this);
		this.gotoKernel = this.gotoKernel.bind(this);
	}

	setKernelList(res) {
		if(res.body['status'] === 0)
		{
			let kernel_list = res.body['list'];
			this.setState(prevState => ({...prevState, error:'', kernel_list:kernel_list}));
		}
		else
		{
			this.setState(prevState => ({...prevState, error:res.body["error"]}));
		}
	}

	setError(error) {
		this.setState(prevState => ({...prevState, error:error.message}));
	}
	
	componentDidMount () {
		agent.csynRequests.kernelList(this.props.location.state.moduleName, this.props.location.state.moduleVersion, this.props.location.state.moduleType, this.setKernelList, this.setError);
	}
	
	gotoKernel(event, rowData) {
		this.setState(prevState => ({...prevState, redirect:1, redirect_kernelName:rowData.kernelName, redirect_moduleName:rowData.moduleName, redirect_moduleVersion: rowData.moduleVersion, redirect_moduleType: rowData.moduleType}));
	}
	
	render() {
		if (this.state.redirect === 1) {
			return <Redirect to={{pathname: '/kernel', state: {kernelName: this.state.redirect_kernelName, moduleName: this.state.redirect_moduleName, moduleVersion:this.state.redirect_moduleVersion, moduleType:this.state.redirect_moduleType }}}/>
		}
		
  	  	return (
			<div>
				<Grid container spacing={3} direction="column" alignContent="center">
					<Grid item>
    					<MaterialTable title="Kernel List" columns={this.state.kernel_list_columns} data={this.state.kernel_list} options={{search: false}}
								actions={[{ icon: 'info', tooltip: 'Kernel details', onClick: this.gotoKernel}]}/>
					</Grid>
					<Grid item>
						<Typography variant="body2">{this.state.error}</Typography>
					</Grid>
				</Grid>
			</div>
		);
	}
}

export default KernelList;