import superagentPromise from 'superagent-promise';
import _superagent from 'superagent';

// Agent that interacts with Optimizer Service API
// https://visionmedia.github.io/superagent/
// https://www.npmjs.com/package/superagent-promise  and https://github.com/lightsofapollo/superagent-promise

const superagent = superagentPromise(_superagent, global.Promise);

const API_ROOT = '';
const encode = encodeURIComponent;

const requests = {
  get: (url, res, err) =>
    superagent.get(`${API_ROOT}${url}`).then(res, err),
  post: (url, query, res, err) =>
    superagent.post(`${API_ROOT}${url}`, query).then(res, err),
};

const csynRequests = {
  deviceList: (res, err) =>
    requests.get(`json/device_list`, res, err),
  device: (ipAddress, port, res, err) =>
    requests.get(`json/device?ipAddress=${encode(ipAddress)}&port=${port}`, res, err),
  moduleList: (res, err) =>
    requests.get(`json/module_list`, res, err),
  kernelList: (module, moduleVersion, moduleType, res, err) =>
    requests.get(`json/kernel_list?module=${encode(module)}&moduleVersion=${encode(moduleVersion)}&moduleType=${moduleType}`, res, err),
  kernel: (kernel, module, moduleVersion, moduleType, res, err) =>
    requests.get(`json/kernel?kernel=${encode(kernel)}&module=${encode(module)}&moduleVersion=${encode(moduleVersion)}&moduleType=${moduleType}`, res, err),
  jobInfoList: (res, err) =>
    requests.get(`json/job_status_list`, res, err),
  jobInfo: (uuid, res, err) =>
    requests.get(`json/job_status?uuid=${encode(uuid)}`, res, err),
  systemInfo: (res, err) =>
    requests.get(`json/system_info`, res, err)
};

export default {
	requests,
	csynRequests
};
