import React from 'react';
import Button from '@material-ui/core/Button';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import { Link } from 'react-router-dom';

class DocumentationMenu extends React.Component {
   
  constructor(props) {
		super(props);
		this.state = {
			anchorEl:null
		}
		
		this.handleClick = this.handleClick.bind(this);
		this.handleClose = this.handleClose.bind(this);
	}

	handleClick(event) {
		this.setState({anchorEl:event.currentTarget})
	}
	
	handleClose() {
		this.setState({anchorEl:null})
	}

	render() {
		return (
			<div>
				<Button aria-controls="simple-menu" aria-haspopup="true" onClick={this.handleClick} color="inherit">Documentation</Button>
				<Menu id="simple-menu" anchorEl={this.state.anchorEl} keepMounted open={Boolean(this.state.anchorEl)} onClose={this.handleClose} >
					<MenuItem component={Link} to="/html_user_guide/index.html" target="_blank" onClick={this.handleClose} color="inherit">User Guide</MenuItem>
					<MenuItem component={Link} to="/html_cpp/index.html" target="_blank" onClick={this.handleClose} color="inherit">C++ SDK Reference</MenuItem>
					<MenuItem component={Link} to="/html_cs/index.html" target="_blank" onClick={this.handleClose} color="inherit">C# SDK Reference</MenuItem>
					<MenuItem component={Link} to="/html_python/index.html" target="_blank" onClick={this.handleClose} color="inherit">Python SDK Reference</MenuItem>
				</Menu>
			</div>
		);
	}
}

export default DocumentationMenu;