import React from 'react';
import MaterialTable from 'material-table';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import { Redirect } from 'react-router-dom'
import agent from '../agent';


class JobInfoList extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
	    	job_info_list_columns: [
				{ title: 'Job Name', field: 'jobName' },
	      	  	{ title: 'UUID', field: 'uuid' },
				{ title: 'Start Time', field: 'startTimestamp'},
				{ title: 'End Time', field: 'endTimestamp' },
				{ title: 'Execution Time', field: 'execution' },
				{ title: 'Status', field: 'executionStatus' },
				{ title: 'Execution Error', field: 'executionError' }
	    	],
			
			job_info_list: [],
			error: '',
			redirect: 0,
			redirect_uuid: '',
		}
		
		this.setJobInfoList = this.setJobInfoList.bind(this);
		this.setError = this.setError.bind(this);
		this.gotoJobInfo = this.gotoJobInfo.bind(this);
	}

	setJobInfoList(res) {
		if(res.body['status'] === 0)
		{
			let job_info_list = res.body['list'];
			this.setState(prevState => ({...prevState, error:'', job_info_list:job_info_list}));
		}
		else
		{
			this.setState(prevState => ({...prevState, error:res.body["error"]}));
		}
	}

	setError(error) {
		this.setState(prevState => ({...prevState, error:error.message}));
	}
	
	componentDidMount () {
		agent.csynRequests.jobInfoList(this.setJobInfoList, this.setError);
	}
	
	gotoJobInfo(event, rowData) {
		this.setState(prevState => ({...prevState, redirect:1, redirect_uuid:rowData.uuid}));
	}
	
	render() {
		if (this.state.redirect === 1) {
			return <Redirect to={{pathname: '/job_status', state: {uuid: this.state.redirect_uuid}}}/>
		}
		
  	  	return (
			<div>
				<Grid container spacing={3} direction="column" alignContent="center">
					<Grid item>
    					<MaterialTable title="Job Status List" columns={this.state.job_info_list_columns} data={this.state.job_info_list} options={{search: false}}
								actions={[{ icon: 'info', tooltip: 'Job stat', onClick: this.gotoJobInfo}]}/>
					</Grid>
					<Grid item>
						<Typography variant="body2">{this.state.error}</Typography>
					</Grid>
				</Grid>
			</div>
		);
	}
}

export default JobInfoList;